import React from 'react'
import './profile.css'
import profile_icon from '../../assets/img2.jpg'

const UserProfileCard = () => {
  return (
    <div className='user-profile-card'>
      <div className="gradient"></div>
      <div className="profile-bottom"></div>
      <img src={profile_icon} height="200px" alt=''/>
      <div className='profile-name'>User Name</div>
      <div className="profile-bio">
        An ambitious and dynamic college student at VIT, dedicated to academic excellence and actively engaged in extracurricular pursuits.
      </div>
      <div className="profile-contact"><a href="mailto:anonymous@unknown.com">Contact Me</a></div>
    </div>
  )
}

export default UserProfileCard